.body{
  width: 100%;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  background-color: #282C34;
  font-family: 'Courier New', monospace;
  color: whitesmoke;
  font-size: 1.2em;
}

.terminal *{
  white-space: pre-wrap;
}

.header{
  width: 100%;
  min-height: 10rem;
  margin: 3rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header a{
  text-decoration: none;
  color: cornflowerblue;
}

.terminal {
  background-color: #000;
  color: lime;
  font-family: 'Courier New', monospace;
  padding: 20px;
  text-align: left;
  max-width: 800px;
  width: 80%;
  height: 30rem;
  border-radius: 5px;
  box-shadow: 0 0 20px 5px #00000080;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  font-size: .8em;
}

::-webkit-scrollbar{
  display: none;
}

.inputLine {
  display: flex;
  align-items: top;
}

.terminalInput {
  background: transparent;
  border: none;
  color: lime;
  outline: none;
  width: 100%;
  font-family: 'Courier New', monospace;
  font-size: 1em;
}

/* Hangman.module.css */

.hangman {
  font-family: monospace;
  white-space: pre;
}

.hangmanConsole {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.hangmanConsole pre {
  margin: 0;
}

